<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process245"></a>

<p><br></p>
<p><span class="rvts17">一个不太熟悉的主顾（</span><span class="rvts50">An Unlikely Patron</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:303,Y:961</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：亚历山大</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助亚历山大干掉瘦黄男人</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1366.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">亚历山大在复仇女神号遭遇达利斯袭击后，瞬间人间蒸发，后来在无名岛的精灵营地上层可以找到他。如果加雷斯此刻在场，会与他发生口角，选择帮助加雷斯杀掉亚历山大，那么将不会引出该支线内容。</span>
<br><span class="rvts21">劝服加雷斯或站在亚历山大一边杀掉加雷斯后（如果伊凡在队伍中，也要及时阻拦他杀掉亚历山大），亚历山大会给我们一顶巫师帽，附带</span><span class="rvts20"></span><span class="rvts21">真实视野</span><span class="rvts20"></span><span class="rvts21">技能，用它可以看透虚假的墙壁。亚历山大会让我们杀掉黑环首领</span><span class="rvts20"></span><span class="rvts21">黄瘦男人</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1367.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">瘦黄男人是黑环势力的领袖，他藏在人类祭坛不远处的一个山洞里。山洞门口有一只巨魔看守。巨魔可以被劝服并为你让路。如果和他发生打斗，需要注意，它会在每轮内生命恢复</span><span class="rvts20">3000</span><span class="rvts21">左右，因此尽可能的在一次战斗回合内，干掉它。</span>
<br><span class="rvts21">进入洞穴，使用头罩中的</span><span class="rvts20"></span><span class="rvts21">真视</span><span class="rvts20"></span><span class="rvts21">技能，可以在祭坛处发现隐藏的道路。从这条道路一直走下去，就会遇到瘦黄男人。杀掉瘦黄男人，然后找亚历山大领赏，亚历山大会告诉你一些祭坛的日月派系。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



